import React from 'react';

import utils from '../../utils';

import './style.css';

const imgs = utils.newArray(4).map(i => {
  return {
    src: require(`../../sources/homepage/view2_${i + 1}.png`).default,
    backgroundColor: i % 2 === 0 ? '#fee452' : '#ffec7a',
    title: utils.localize(`view2.img.title${i + 1}`)
  };
});

class View2 extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="view-container view2">
        <div className="view-background"></div>
        <div className="view-background white"></div>
        <div className="view-content-container">
          <div className="view-tag">{utils.localize('view2.tag')}</div>
          <div className="view-content-text-container">
            <h2>{utils.localize('view2.title')}</h2>
            <p>{utils.localize('view2.content')}</p>
          </div>
          <div className="view-content-imgs-container">
            {
              imgs.map((img, i) => {
                return (<div key={i} className="view-content-img" style={{backgroundColor: img.backgroundColor}}>
                  <img src={img.src} />
                  <h2>{img.title}</h2>
                </div>);
              })
            }
          </div>
        </div>
      </div>
    );
  }
}

export default View2;