import React from 'react';
import './layer.css';

class Layer extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const {className, onHideLayer} = this.props;
    return (
      <div className={`layer ${className ? className : ''}`} onClick={onHideLayer}>
        {this.props.children}
      </div>
    );
  }
}

export default Layer;