import local_en from './localization/localization-en';
import local_zh from './localization/localization-ch';

const lang = window.localStorage.getItem('leading_lang');
let local = local_zh;
if (lang === 'en') {
  local = local_en;
}

const utils = {
  localize: (key) => {
    return local[key] ? local[key] : key;
  },
  newArray: (size) => {
    let result = [];
    for (let i = 0; i < size; i++) {
      result.push(i);
    }
    return result;
  }
};

export default utils;
