import React from 'react';

import utils from 'src/utils';

import './imgSwitcher.css';

const DEFAULT_TIMEOUT = 4000;

const lang = window.localStorage.getItem('leading_lang') || 'zh';

const slides = utils.newArray(3).map(i => {
  return {
    src: require(`src/sources/homepage/banner${i + 1}_${lang}.jpg`).default
  };
});

class ImgSwitcher extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      index: 0
    };

    this.timer = null;
  }

  componentDidMount() {
    this.autoSwitchImg();
  }

  switchImg(index) {
    clearInterval(this.timer);
    this.setState({
      index: index
    }, () => {
      this.autoSwitchImg();
    });
  }

  autoSwitchImg() {
    const {items, autoSwitch, autoSwitchTimeout} = this.props;
    const {index} = this.state;
    if (this.props.autoSwitch) {
      this.timer = setInterval(() => {
        this.switchImg((index + 1) % items.length);
      }, autoSwitchTimeout || DEFAULT_TIMEOUT);
    }
  }

  render() {
    const {title, items} = this.props;
    const {index} = this.state;
    return (
      <div className="img-switcher">
        <img
          className="img-switcher-img height-holder"
          src={items[0].src}
        />
        {
          items.map((item, i) => {
            if (i === index) {
              return (<img
                key={'img' + i}
                className="img-switcher-img"
                src={item.src}
              />);
            }
            return null;
          })
        }
        <div className="switcher-button-container">
          {
            items.map((item, i) => {
              return (<div
                key={i}
                className={`switcher-button ${(i === index ? 'selected' : '')}`}
                onClick={this.switchImg.bind(this, i)}
              ></div>);
            })
          }
        </div>
      </div>
    );
  }
}

ImgSwitcher.defaultProps = {
  items: slides
};

export default ImgSwitcher;

ImgSwitcher.defaultProps = {
  items: slides
};
