import React from 'react';
import './style.css';
import utils from 'src/utils';
import Icon from 'src/components/icon';
import { withRouter } from 'react-router-dom';

class Menu extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      showFullMenu: false,
      menuIsHidden: false
    };

    this.jumpTo = this.jumpTo.bind(this);
    this.showMenu = this.showMenu.bind(this);
    this.hideMenu = this.hideMenu.bind(this);
    this.showMenuTemp = this.showMenuTemp.bind(this);
    this.hideMenuTemp = this.hideMenuTemp.bind(this);
    this.changeLocal = this.changeLocal.bind(this);
  }

  componentDidMount() {
    // document.addEventListener('scroll', (e) => {
    //   const scrollTop = e.target.scrollingElement.scrollTop;
    //   if (scrollTop > 400) {
    //     this.hideMenu();
    //   } else {
    //     this.showMenu();
    //   }
    // });
  }

  jumpTo(link) {
    this.props.history.push({pathname: link});
  }

  showMenu() {
    if (this.state.menuIsHidden) {
      this.setState({
        menuIsHidden: false
      });
    }
  }

  hideMenu() {
    if (!this.state.menuIsHidden) {
      this.setState({
        menuIsHidden: true
      });
    }
  }

  showMenuTemp() {
    this.setState({
      showFullMenu: true
    });
  }

  hideMenuTemp() {
    this.setState({
      showFullMenu: false
    });
  }

  changeLocal() {
    let lang = window.localStorage.getItem('leading_lang');
    window.localStorage.setItem('leading_lang', lang === 'zh' ? 'en' : 'zh');
    window.location.reload();
  }

  render() {
    const {showFullMenu, menuIsHidden} = this.state;
    let isHomePage = this.props.location.pathname === '/';
    return (
      <div className={`menu ${isHomePage ? 'home-page-menu' : ''}`}>
        {
          menuIsHidden && !showFullMenu ?
            <Icon className="show-menu-button" name="menu" size={22} onClick={this.showMenuTemp} /> :
            (<div className="menu-container">
              <div className="menu-logo-container" onClick={this.jumpTo.bind(this, '/')}>
                <img src={require('../../sources/leading_logo.png').default} />
              </div>
              <div className="menu-items-container">
                {
                  this.props.items.map((item, i) => {
                    return (<div className="menu-item" key={`item${i}`}>
                      <div className="item-name" onClick={this.jumpTo.bind(this, item.link)}>{item.name}</div>
                    </div>);
                  })
                }
              </div>
              <div className="menu-local-change" onClick={this.changeLocal}>{utils.localize('changeLocal')}</div>
            </div>)
        }
        {
          showFullMenu && menuIsHidden ? 
            <Icon className="hide-menu-button" name="close" size={22} color={isHomePage ? '#fff' : undefined} onClick={this.hideMenuTemp} /> :
            null
        }
      </div>
    );
  }
}

export default withRouter(Menu);