import React from 'react';
import Icon from 'src/components/icon';
import utils from 'src/utils';
import './imgSlides.css';

const DEFAULT_TIMEOUT = 2000;

class ImgSlides extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      index: 0,
      items: props.items
    };
  }

  componentDidMount() {
    this.timer = setInterval(() => {
      this.switchImg(this.state.index + 1);
    }, DEFAULT_TIMEOUT);
  }

  switchImg(index) {
    let newItems = [...this.state.items];
    let item = newItems[this.state.index];
    newItems.push(item);
    this.setState({
      index: index,
      items: newItems
    });
  }

  renderSlides() {
    const {index, items} = this.state;
    let className = 'img-slide-wall';
    return (<div className={className}>
      {
        items.map((di, i) => {
          if (i - index < -1) {
            return null;
          }
          return (<div
            className="img-slide-img-container"
            style={{left: ((i - index) * 210 + 'px')}}
            key={`img${i}`}
          >
            <img className="img-slide-img" src={di.src} />
          </div>);
        })
      }
    </div>);
  }

  render() {
    const {height} = this.props;
    return (
      <div className="img-slide-container" style={{height: height}}>
        {this.renderSlides()}
      </div>
    );
  }
}

export default ImgSlides;