import React from 'react';

import ImgSlides from '../imgSlides';
import utils from '../../utils';

import './style.css';

let imgs = utils.newArray(15).map(i => {
  return {
    src: require(`../../sources/homepage/logos/${i + 1}.png`).default
  };
});

class View4 extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="view-container view4">
        <div className="view-content-container">
          <div className="view-content-text-container">
            <h2>{utils.localize('view4.title')}</h2>
          </div>
          <div className="view-logo-slide-container">
            <ImgSlides items={imgs} height={100} />
          </div>
        </div>
      </div>
    );
  }
}

export default View4;