import React from 'react';

import utils from '../../utils';

import './style.css';

const imgs1 = utils.newArray(4).map(i => {
  return {
    src: require(`../../sources/homepage/view3_${i + 1}.jpg`).default
  };
})

const imgs2 = utils.newArray(4).map(i => {
  return {
    src: require(`../../sources/homepage/view3_${i + 5}.jpg`).default
  };
})

class View3 extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="view-container view3">
        <div className="view-background"></div>
        <div className="view-content-container">
          <div className="view-tag">{utils.localize('view3.tag')}</div>
          <div className="view-content-text-container">
            <h2>{utils.localize('view3.title')}</h2>
            <p>{utils.localize('view3.content')}</p>
          </div>
          <div className="view-content-imgs-container">
            <div className="view-content-imgs-row">
              {
                imgs1.map((img, i) => {
                  return <img key={i} src={img.src} />;
                })
              }
            </div><div className="view-content-imgs-row">
            {
              imgs2.map((img, i) => {
                return <img key={i} src={img.src} />;
              })
            }
          </div>
          </div>
        </div>
      </div>
    );
  }
}

export default View3;