import React from 'react';

const default_color = '#5a5a5a';
const default_size = 16;

class Icon extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const {name, size, color, onClick, className} = this.props;
    let thisSize = size || default_size;
    const Svgicon = require(`../svg/${name}.svg`).default;
    return (
      <div
        className={`icon ${className ? className : ''}`}
        style={{width: thisSize, height: thisSize}}
        onClick={onClick}
      >
        <Svgicon viewBox="0 0 32 32" fill={color || default_color} />
      </div>
    );
  }
}

export default Icon;
